.payments__form {
  margin-bottom: 50px; }

.payments__intro {
  font-size: 12px; }
  @media screen and (min-width: 48em) {
    .payments__intro {
      font-size: inherit;
      margin-bottom: 22px; } }

.payments__fieldset {
  padding: 20px 22px 0;
  margin-bottom: 25px;
  border-top: 1px solid #e6e6e6; }
  @media screen and (min-width: 48em) {
    .payments__fieldset {
      padding-top: 38px;
      margin-bottom: 15px; } }

.payments__fieldset-item {
  margin-bottom: 10px; }
  .payments__fieldset-item input,
  .payments__fieldset-item select, .payments__fieldset-item:last-child {
    margin-bottom: 0; }
  .payments__fieldset-item label span {
    color: #b3b3b3; }
  @media screen and (min-width: 48em) {
    .payments__fieldset-item {
      display: table;
      width: 100%;
      margin-bottom: 15px; }
      .payments__fieldset-item label {
        width: 28%;
        padding-right: 20px;
        display: table-cell;
        vertical-align: middle;
        text-align: right; }
      .payments__fieldset-item input,
      .payments__fieldset-item select {
        display: table-cell;
        width: 350px;
        vertical-align: middle; } }

.payments__btn-wrap {
  padding: 10px 0; }
  .payments__btn-wrap button[type='submit'],
  .payments__btn-wrap input[type='submit'] {
    width: 100%; }
  @media screen and (min-width: 48em) {
    .payments__btn-wrap {
      width: 350px;
      display: table-cell;
      padding: 0 0 0 20px; }
      .payments__btn-wrap button[type='submit'],
      .payments__btn-wrap input[type='submit'] {
        width: auto;
        padding-left: 41px;
        padding-right: 41px; } }
